import * as actionTypes from "../actions/actions";

const initialState = {
  distance: 0,
  duration: 0,
};

const costReducer = (state = initialState, action) => {
  switch (action.type) {
    case actionTypes.COST:
      return {
        ...state,
        distance:action.cost.distance,
        duration:action.cost.duration,
      };
//       case actionTypes.ONINIT_DATA:
//       return {
//         state,
//       };
    default:
      return state;
  }
};

export default costReducer;
