import React, { Component } from "react";
import { GoogleMap, LoadScript } from "@react-google-maps/api";
import MapStyle from "../Map/MapStyle";
import "../Map/map.css";
import "@reach/combobox/styles.css";
import Dashboarddirection from "./dashboarddirection";

const containerStyle = {
  width: "40vw",
  height: "50vh",
};

const options = {
  styles: MapStyle,
  disableDefaultUI: true,
};

const libraries = ["places"];

class Dashboardmap extends Component {
  state = {
    isSubmitted: false,
    mapRef: null,
  };
  lat = this.props.latorigin;
  lng = this.props.lngorigin;

  style = { width: "140px", marginLeft: `${window.innerWidth - 140}px` };
  style1 = { width: "140px", marginLeft: `${window.innerWidth - 240}px` };

  render() {
    const panTo = ({ lat, lng }) => {
      this.state.mapRef.panTo({ lat, lng });
      this.state.mapRef.setZoom(14);
    };
    const api = process.env.REACT_APP_GOOGLE_MAPS_API_KEY;

    return (
      <div className="map">
        {console.log(this.props)}
        <LoadScript googleMapsApiKey={api} libraries={libraries}>
          <GoogleMap
            onLoad={(map) => {
              this.setState({ mapRef: map });
            }}
            mapContainerStyle={containerStyle}
            center={{ lat: this.lat, lng: this.lng }}
            zoom={12}
            options={options}
            panTo={panTo}
          >
            <Dashboarddirection
              latorigin={this.props.latorigin}
              lngorigin={this.props.lngorigin}
              latdestination={this.props.latdestination}
              lngdestination={this.props.lngdestination}
            />
          </GoogleMap>
        </LoadScript>
      </div>
    );
  }
}

export default Dashboardmap;
