import React, { Component } from "react";
import { DirectionsService, DirectionsRenderer } from "@react-google-maps/api";

class Dashboarddirection extends Component {
  shouldComponentUpdate(nextProps, nextState) {
    if (
      this.state.response===nextState.response
    ) {
      return false;
    } else return true;
  }
  state = {};
  render() {
    const destination = {
      lat: this.props.latdestination,
      lng: this.props.lngdestination,
    };
    const origin = {
      lat: this.props.latorigin,
      lng: this.props.lngorigin,
    };

    const directionsResponse = (response) => {
      if (response !== null) {
        if (response.status === "OK") {
          this.setState({
            response: response,
          });
        }
      }
    };

    return (
      <div>
        {console.log(this.state)}
        <DirectionsService
          options={{
            destination: destination,
            origin: origin,
            travelMode: "DRIVING",
          }}
          callback={directionsResponse}
        />
        {this.state.response !== null && (
          <DirectionsRenderer
            options={{
              directions: this.state.response,
            }}
          />
        )}
      </div>
    );
  }
}

export default Dashboarddirection;
