import React, { useState } from "react";
import girlModel from "../../assets/images/modelGirl.jpeg";
import boyModel from "../../assets/images/model.jpeg";

import { Button, Media } from "react-bootstrap";
import { LinkContainer } from "react-router-bootstrap";
import axios from "axios";

import Dashboardmap from "./dashboardmap";

function TripMedia({ trip, group, otherGroup }, props) {
  const [picklocation, setpicklocation] = useState([]);
  const [droplocation, setdroplocation] = useState([]);
  const user = trip[otherGroup];
  const href = group ? `/${group}/${trip.id}` : undefined;
  const api = process.env.REACT_APP_GOOGLE_MAPS_API_KEY;
  const pick = JSON.parse(trip.pick_up_address);
  const drop = JSON.parse(trip.drop_off_address);
  const latorigin = pick.latorigin;
  const lngorigin = pick.lngorigin;
  const latdestination = drop.latdestination;
  const lngdestination = drop.lngdestination;

  axios
    .get(
      `https://maps.googleapis.com/maps/api/geocode/json?latlng=${latorigin},${lngorigin}&key=${api}`
    )
    .then((res) => {
      setpicklocation(res.data.results[0].formatted_address);
    });
  axios
    .get(
      `https://maps.googleapis.com/maps/api/geocode/json?latlng=${latdestination},${lngdestination}&key=${api}`
    )
    .then((res) => {
      setdroplocation(res.data.results[0].formatted_address);
    });

  return (
    <Media as="li">
      <img
        alt={user}
        className="mr-3 rounded-circle"
        src={props.group === "rider" ? boyModel : girlModel}
        width={65}
        height={65}
      />
      <Media.Body>
        <h5 className="mt-0 mb-1">
          {trip.rider.first_name} {trip.rider.last_name}
        </h5>
        {picklocation} to {droplocation}
        <br />
        <Dashboardmap
          latorigin={latorigin}
          lngorigin={lngorigin}
          latdestination={latdestination}
          lngdestination={lngdestination}
        />
        {trip.status}
        {href && (
          <LinkContainer to={href}>
            <Button variant="primary" block>
              Detail
            </Button>
          </LinkContainer>
        )}
      </Media.Body>
    </Media>
  );
}

export default TripMedia;
